import React from 'react'
import { NavLink } from 'react-router-dom'
import img from '../../../data/images'

export default function S5HomeBlog() {
  const blogColumn = [
    {link: '/tours', img: img.home_blog_1, caption: '2  2020.', subTitle: '5    ', text: '   .            ,     ...'},
    {link: '/tours', img: img.home_blog_2, caption: '12  2020.', subTitle: ' ', text: '  -   !   ,       ,      .     ...'},
    {link: '/tours', img: img.home_blog_3, caption: '23  2020.', subTitle: '-', text: '- -       ,      .       ,     ... '},
   
  ]
  return (
    <section className="blog">
    <div className="blog-bigimage">
      <picture>
        <source srcSet={img.home_section_3_tablet} media="(max-width:768px)"/>
        <img src={img.home_section_3} alt=""/>
      </picture>
    </div>
    <div className="container">
      <h1 className="blog-title title-h1"> </h1>
      <div className="blog-row">
        {
          blogColumn.map((item, i)=> { 
           return ( 
            <NavLink key={i} to={item.link} className="blog-column">
            <div className="blog-column__image"><img src={item.img} alt=""/></div>
            <div className="blog-column__caption">{item.caption}</div>
            <div className="blog-column__subtitle title-h2">{item.subTitle}</div>
            <div className="blog-column__text textBody">{item.text}</div>
          </NavLink>
            )})  
        }
  </div>

    </div>


  </section>
  )
}
