import React, { useRef } from 'react'
import icon from '../../data/icons'
import img from '../../data/images'
import SocialIcons from '../SocialIcons/SocialIcons'
export default function Footer() {
  const footerInput = useRef()
 return (
 <>
 <section className="footer">
      <div className="container">
        <div className="footer-row">
          <div className="footer-left"><img src={img.home_footer} alt=""/></div>
          <div className="footer-right">
            <h1 className="footer-right__title"> &nbsp;</h1>
            <div className="footer-right-columns">
              <div className="footer-right-columns__left">,         
                 </div>
              <div className="footer-right-columns__right">
                <input ref={footerInput} type="text" placeholder=" E-mail:"/>
                <div
                onClick={()=> footerInput.current.value = ''}
                className="footer-right-columns__icon"><img src={icon.arrow_footer} alt=""/></div>
              </div>
            </div>
          </div>
        </div>
        <div className="footer-bottom">
          <div className="footer-bottom__row">
            <a href="index.html" className="footer-bottom__logo"><img src={icon.logo} alt=""/></a>
            <div className="footer-bottom__adress">. , <span>21/5, . , +7 495 626-05-48</span> </div>
            <div className="footer-bottom-social__row">
              <SocialIcons
              styles={'footer-bottom-social__icon'}
              />
            </div>
          </div>
        </div>
      </div>
    </section>
</>
)
}
