import React, { useEffect } from 'react'
import img from '../../../data/images'
import SlideMargin from '../../../components/SlideMargin/SlideMargin'

export default function S1AboutLets() {

  useEffect(()=> {
    const letsleftsmallImage = document.querySelector('.lets-left__smallImage'); 
 window.addEventListener('scroll', function () {
 letsleftsmallImage.style.bottom = window.scrollY / 4 + 'px';
 });
  })

 
  SlideMargin('.lets-row')
  return (
    <section className="lets">
      <div className="lets-row">
        <div className="lets-left__row">
          <div className="lets-left__bigImage"><img src={img.aboutFirstSection} alt=""/></div>
          <div className="lets-left__smallImage"><img src={img.about_section1_small} alt=""/></div>
        </div>
        <div className="lets-right__row">
          <h1 className="lets-right__title title-h1">
             </h1>
          <div className="lets-right__subTitle">Travel House,   2006 ,   ,
               ,  . </div>
          <div className="lets-right__text textBody">      Travel House    
             ,       .    
                      - Russia Travel Excellence Award 2008. <p>
            </p>
             ,      Customer-Centric Initiative (CCI)  
                 . <p></p>
               ,    ,     ,   
               . </div>
        </div>
      </div>
    </section>
  )
}
