import React from 'react'
import { NavLink } from 'react-router-dom'
import img from '../../../data/images'

export default function S4HomeStart() {
  const startBottomColumn = [
    {link: '/tours', img: img.home_start_1, text: ''},
    {link: '/tours', img: img.home_start_2, text: ''},
    {link: '/tours', img: img.home_start_3, text: ''},
  ]
  const rightText = [
'   ',
"5  ",
'  ',
'   '
]
  return (
    <section className="start">
      <div className="container">
        <h1 className="start__title ">  </h1>
        <h2 className="start__subTitle title-h1">  Travel House</h2>
        <div className="start-row">
          <div className="start-left textBody">     ,   
                ,   .     
            ,        ,   
             ,            ,  ,
             , ,   20  . </div>
          <div className="start-right">
            <ol className="start-right__row textBody">
              {
                rightText.map((item, i)=> { 
                 return ( 
                  <li key={i} className="start-right__text">{item}</li>
                  )})  
              }
 

            </ol>
          </div>
        </div>

        <div className="start-bottom">
          <div className="start-bottom__row">
            {
              startBottomColumn.map((item, i)=> { 
               return ( 
                <NavLink key={i} to={item.link} className="start-bottom-column">
                <div className="start-bottom-column__image"><img src={item.img} alt=""/>
                  <div className="start-bottom-column__text">{item.text}</div>
                </div>
  
              </NavLink>
                )})  
            }

           
          </div>
          <div className="start-bottom-button"><NavLink to="/tours"> </NavLink></div>

        </div>

      </div>
    </section>
  )
}
