import React from 'react'
import { NavLink } from 'react-router-dom'
import img from '../../../data/images'

export default function S2Discount() {

  const discItem = [
    {  img: img.discount_section_2_1, title: ' ', text: '   ?      .', link: '/discount-single/mountains' },
    {  img: img.discount_section_2_2, title: '  ', text: '             .', link: '/discount-single/couple' },
    {  img: img.discount_section_2_3, title: '  ', text: '         .', link: '/discount-single/beach' },
    {  img: img.discount_section_2_4, title: '', text: '           .', link: '/discount-single/cruise' },
    {  img: img.discount_section_2_5, title: '', text: '    -       .', link: '/discount-single/safari' },
    {  img: img.discount_section_2_6, title: '', text: '       .    .', link: '/discount-single/rafting' },

  ]

  return (
    <section className="disc-tours">
    <div className="container">
      <div className="disc-tours-row">
        {
          discItem.map((item, i)=> { 
           return ( 
            <div key={i} className="disc-tours-column">
            <div className="disc-tours__image"><img src={item.img} alt=""/></div>
            <div className="disc-tours__title title-h2">{item.title}</div>
            <div className="disc-tours__text textBody">{item.text}</div>
            
            <NavLink to={item.link} className="disc-tours__btn"> ...</NavLink>
          </div>
            )})  
        }
  
     
  
  
      </div>
    </div>
  </section>
  )
}
